.range {
  -webkit-appearance: none;
  width: 100%;
  margin: 0;
  margin-top: 30px;
}
.range:focus {
  outline: none;
}
.range::-webkit-slider-runnable-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 0.2px #000000, 0px 0px 0px #000000;
          box-shadow: 0px 0px 0.2px #000000, 0px 0px 0px #000000;
  background: #ebebeb;
  border-radius: 0px;
  border: 0px;
}
.range::-webkit-slider-thumb {
  -webkit-box-shadow: 0px 0px 1px #45b3f8, 0px 0px 0px #45b3f8;
          box-shadow: 0px 0px 1px #45b3f8, 0px 0px 0px #45b3f8;
  border: 0px solid #45b3f8;
  height: 15px;
  width: 15px;
  border-radius: 0px;
  background: #45b3f8;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: 0px;
}
.range:focus::-webkit-slider-runnable-track {
  background: #ebebeb;
}
.range::-moz-range-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  box-shadow: 0px 0px 0.2px #45b3f8, 0px 0px 0px #45b3f8;
  background: #ebebeb;
  border-radius: 0px;
  border: 0px solid #ebebeb;
}
.range::-moz-range-thumb {
  box-shadow: 0px 0px 1px #45b3f8, 0px 0px 0px #45b3f8;
  border: 0px solid #45b3f8;
  height: 15px;
  width: 15px;
  border-radius: 0px;
  background: #45b3f8;
  cursor: pointer;
}
.range::-ms-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.range::-ms-fill-lower {
  background: #ebebeb;
  border: 0px solid #45b3f8;
  border-radius: 0px;
  box-shadow: 0px 0px 0.2px #45b3f8, 0px 0px 0px #45b3f8;
}
.range::-ms-fill-upper {
  background: #ebebeb;
  border: 0px solid #45b3f8;
  border-radius: 0px;
  box-shadow: 0px 0px 0.2px #45b3f8, 0px 0px 0px #45b3f8;
}
.range::-ms-thumb {
  box-shadow: 0px 0px 1px #45b3f8, 0px 0px 0px #45b3f8;
  border: 0px solid #45b3f8;
  width: 15px;
  border-radius: 0px;
  background: #45b3f8;
  cursor: pointer;
  height: 15px;
}
.range:focus::-ms-fill-lower {
  background: #ebebeb;
}
.range:focus::-ms-fill-upper {
  background: #ebebeb;
}



